<template>
<div>
<div class="headpic" v-for="da in data" v-bind:key="da.id" style="width:100px; height:100px; margin:20px;">{{da.data.yes.headImage}}</div>
</div>
</template>

<script>
export default {
  name: 'home',
  props:['number','cate'],
  data () {
    return {
      data: ''
    }
  },
  mounted () {
    let _this = this
    _this.$http({
      method: 'post',
      url: '/api/weekly/user/getUserList.action',
      params: {
        'id': _this.number,
        'category': _this.cate
      }
    })
  }
}
</script>

<style>

</style>



